$('.video-block').on('click', function(e){
	e.preventDefault();
	$(this).hide();
	$(this).parent().append('<iframe class="frame video-wrap__item" width="100%" src="https://www.youtube.com/embed/NJHcdKf5iHs?autoplay=1" frameborder="0" allowfullscreen></iframe>');

	video();
});

function video(){
	var height = $('.video-wrap__item').width() * .7;
	$('.video-wrap__item').height(height);
}

$(window).resize(function(){
	video();
})