$('.content__link').on('click', function(e){
	e.preventDefault();
	$(this).toggleClass('content__link--active');
	$(this).siblings('.content__text-hide').fadeToggle()

	if($(this).hasClass('content__link--active')){
		$(this).text('Свернуть статью');
	}else{
		$(this).text('Читать полностью');
	}
})

$('#news-link').on('click', function(){
	$('.content__title-image').toggleClass('content__title-image--active')
})