 $('.slider-for').slick({
  slidesToShow: 1,
  slidesToScroll: 1,
  arrows: false,
  fade: true,
  asNavFor: '.card-block__bot'
})

$('.card-block__bot').slick({
	slidesToShow: 4,
	slidesToScroll: 1,
  asNavFor: '.slider-for',
  focusOnSelect: true,
	responsive: [
	{
		breakpoint: 1199,
		settings: {
			slidesToShow: 3,
			slidesToScroll: 3,
		}
	},
	{
		breakpoint: 991,
		settings: {
			slidesToShow: 4,
			slidesToScroll: 4,
		}
	},
	{
		breakpoint: 512,
		settings: {
			slidesToShow: 3,
			slidesToScroll: 3,
		}
	},
	]
})

$('.slider-for__item').fancybox({
	wrapCSS: 'fancybox-new'
});