$('.select__current').click(function(){ 
	$('.select__list').not($(this).siblings('.select__list')).fadeOut(); 
	$('.select').not($(this).parents('.select')).removeClass('select_active'); 
	$(this).siblings('.select__list').fadeToggle(); 
	$(this).parents('.select').toggleClass('select_active'); 
}); 

$('.select__item').click(function(){ 
	var text = $(this).text(), 
	val = $(this).attr('data-val'); 
	$(this).parents('.select').find('.select__current').text(text).attr('data-val', val); 
	$(this).parents('.select__list').fadeToggle(); 
	$(this).parents('.select').toggleClass('select_active'); 
});

$('.datepicker-here').datepicker({
	onSelect: function(formattedDate, date, inst){
		var $periodBlock = $('.order-block__period'),
				$inputParent = $(inst.el).parents('.order-block__select');
		if ($inputParent.find('.datepicker-from').val() != '' && $inputParent.find('.datepicker-to').val() != '') {
			var date1 = $inputParent.find('.datepicker-from').val().split('.');
			var newDate1 = date1[2] + '-' + date1[1] + '-' + date1[0]
			var date2 = $inputParent.find('.datepicker-to').val().split('.');
			var newDate2 = date2[2] + '-' + date2[1] + '-' + date2[0]
			var diff = dateDiff(newDate1, newDate2);
			if (diff[1] == 0) {
				$periodBlock.text('Период: ' + diff[2] + ' сут.')
			}else if(diff[1] > 0){
				$periodBlock.text('Период: ' + diff[1] + ' мес. и ' + diff[2] + ' сут.')
			}
			// dateDiff(date1, date2)
		}
	}
})

$('.order-block__del').on('click', function(){
	$(this).parent().remove();

	if ($('.order-block').find('.order-block__inner').length) {
	}else{
		$('.order-block').hide()
	}
})

function priceFirstInit(){
	var $priceBlock = $('.order-block__price'),
			sum = 0;
		$('.order-block__select').each(function(index){
			if ($(this).find('.select').length) {
				var $current = $(this).find('.select__current');
				sum = sum + $current.attr('data-val') * $current.data('price');
				if (index == $('.order-block__select').length - 1) {
					$priceBlock.text(sum+' руб.');
				}
			}
		})
}

priceFirstInit();

function priceInit(){
	var $priceBlock = $('.order-block__price'),
			sum = 0;
	$('.order-block__select .select__item').click(function(){
		sum = 0;
		$('.order-block__select').each(function(index){
			if ($(this).find('.select').length) {
				var $current = $(this).find('.select__current');
				sum = sum + $current.attr('data-val') * $current.data('price');
				if (index == $('.order-block__select').length - 1) {
					$priceBlock.text(sum+' руб.');
				}
			}
		})
	})
}

priceInit();