function tabInit(){ 
	$('.tabs__content').not('.tabs__content_active').hide(); 
	$('.tabs-list__item button').click(function(){
		$this = $(this)
		$('.tabs-list__item').not($(this).parent()).removeClass('tabs-list__item_active'); 
		$(this).parent().addClass('tabs-list__item_active'); 
		$('.tabs__content').not('#'+$(this).attr('data-content')).removeClass('tabs__content_active').hide(); 
		$('#'+$(this).attr('data-content')).addClass('tabs__content_active').fadeIn(400, function(){
			if ($('#'+$this.attr('data-content')).find('.tabs-slider').length && !$('#'+$this.attr('data-content')).find('.tabs-slider').is('.slick-initialized')) {
				console.log($('#'+$(this).attr('data-content')).find('.tabs-slider'))
				$('.tabs-slider').slick({
					slidesToShow: 2,
					slidesToScroll: 1,
					responsive: [
					{
						breakpoint: 1199,
						settings: {
							slidesToShow: 1,
						}
					}
					]
				})
			}else if ($('#'+$this.attr('data-content')).find('.tabs-slider').length && $('#'+$this.attr('data-content')).find('.tabs-slider').is('.slick-initialized')){
				$('.tabs-slider').slick('unslick');
				
				$('.tabs-slider').slick({
					slidesToShow: 2,
					slidesToScroll: 1,
					responsive: [
					{
						breakpoint: 1199,
						settings: {
							slidesToShow: 1,
						}
					}
					]
				})
			}
		}); 
	}); 
}; 
tabInit();