$('.order-tovar__btn').on('mouseenter', function(){
	$(this).parents('.order-tovar').find('.order-tovar__image').addClass('order-tovar__image--hover');
})

$('.order-tovar__btn').on('mouseleave', function(){
	$(this).parents('.order-tovar').find('.order-tovar__image').removeClass('order-tovar__image--hover');
})