$('#new-map').hide();
$(document).ready(function(){
	$('.map__tooltip').on('click', function(){
		$(this).siblings('#map').hide();
		$(this).next('#new-map').show();
		$('#new-map').remove();
		$(this).parent().append('<div id="new-map"></div>');
		
		$.post( "map.php", function(data) {
			data = JSON.parse(data);

			var array = [];

			for(var id in data){
				array.push(parseFloat(data[id]));
			}

			function newInit () {
				var myMap = new ymaps.Map('new-map', {
					center: array,
					zoom: 15
				});

				var myPlacemark = new ymaps.Placemark(
					array, {

					}, {
						iconImageHref: 'assets/img/baloon.png',
						iconImageSize: [32, 49],
					});

				myMap.geoObjects.add(myPlacemark);
			}newInit ()
		});
	})
});



